<template>
  <div class="orderDetail">
      <van-nav-bar
      title="订单详情"
      left-text="返回"
      left-arrow
      @click-left="$router.go(-1)"
      />
    <order-display :order="order" />
    <div class="buyerRemark">留言：<br/>{{buyerRemark}}</div>
    <theAddress :address="address"></theAddress>
    <div class="buttons">
      <van-button type="danger" @click="handleClickCancle">{{dynamicText.text[dynamicText.type].a}}</van-button>
      <van-button type="info" @click="handleClickConfirm">{{dynamicText.text[dynamicText.type].b}}</van-button>
    </div>
  </div>
</template>

<script>
import { getOrderDetailApi, cancleOrderApi, confirmReceiveOrderApi } from '@/api/order.js'
import theAddress from '@/components/address.vue'
import orderDisplay from '@/components/orderDisplay.vue'
import { Notify } from 'vant'
export default {
  name: 'orderDetail',
  components: {
    theAddress,
    orderDisplay
  },
  data () {
    return {
      orderId: -1,
      address: {},
      order: {},
      buyerRemark: '',
      dynamicText: {
        type: 0,
        text: [
          { a: '取消订单', b: '确认收货' },
          { a: '取消订单', b: '去付款' }
        ]
      }
    }
  },
  async created () {
    // console.log(this.$route.query.order_id)
    if (this.$route.query.order_id !== undefined) {
      this.orderId = this.$route.query.order_id
      const { data } = await getOrderDetailApi(this.orderId)
      this.order = data.order
      const { order: { address, buyer_remark: buyerRemark } } = data
      this.address = address
      this.buyerRemark = buyerRemark
    }

    // console.log(this.orderId)
  },
  methods: {
    async handleClickCancle () {
      const res = await cancleOrderApi(this.orderId)
      if (res.status === 200) {
        Notify(`已取消订单:${this.order.order_no}`)
        this.$router.replace('/orders')
      }
    },
    async handleClickConfirm () {
      const res = await confirmReceiveOrderApi(this.orderId)
      if (res.status === 200) {
        Notify(`已收货:${this.order.order_no}`)
        this.$router.replace('/orders')
      }
    }
  }
}
</script>

<style scoped>
.buttons{
  display: flex;
  justify-content: center;
}
.van-button{
  margin: 10px auto;
}
.buyerRemark{
  border-bottom: 1px solid black;
  background-color: #e3e6f3;
  margin: 10px 20px;
}
</style>
